import { Dispatch, SetStateAction, useEffect } from "react"
import { ContentCategory } from "@rp/react/tanstack-api/content"
import { UseContentCategoriesListState } from "./useContentCategoriesList"

type UseInitializeContentCategorySelectionProps = {
  contentCategoriesInfo: UseContentCategoriesListState
  currentCategory: ContentCategory | null
  setCurrentCategory: Dispatch<SetStateAction<ContentCategory | null>>
}

function useInitializeContentCategorySelection({
  contentCategoriesInfo,
  currentCategory,
  setCurrentCategory,
}: UseInitializeContentCategorySelectionProps) {
  useEffect(() => {
    if (!currentCategory && contentCategoriesInfo.allContentCategories[0]) {
      setCurrentCategory(contentCategoriesInfo.allContentCategories[0])
    }
  }, [contentCategoriesInfo.allContentCategories, currentCategory, setCurrentCategory])
}

export { useInitializeContentCategorySelection }
